/*TODAS LAS PORTADAS*/

.portada {
    background-size: 100%;
    margin: 0;
    padding-top: 0;
    width: 100%;
    height: 350px;
    background-repeat: no-repeat;

    @media (max-width: 767px) {
        width: 100%;
    }

    @media (max-width: 479px) {
        height: auto;
    }
}